body {
  background-color: #d6e2f0;
  align-content: center;
}

#centre-card {
  width: 320px;
  min-height: 495px;
  text-align: center;
  background-color: #fff;
  border-radius: 25px;
  font-family: Outfit, sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#centre-card h1 {
  color: #1f3251;
  margin: 10px 30px auto;
  font-size: 16pt;
  font-weight: 700;
}

#centre-card p {
  color: #7b879d;
  margin: 15px 30px auto;
  font-size: 12pt;
  font-weight: 400;
}

#qr-code {
  max-width: 90%;
  border-radius: 15px;
  margin: 15px;
}

.attribution {
  font-family: Arial, sans-serif;
  font-size: 8px;
  position: absolute;
  bottom: 0;
  right: 0;
}

